* {
  text-align: center;
}
.main_div {
  background-color: black;
  height: 100vh;
}
.input {
  margin-right: 10px;
  color: black;
}
.input-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.button-add {
  padding-left: 7px;
  padding-right: 7px;
  padding-top: 5px;
  padding-bottom: 5px;
  border-radius: 10px;
  background-color: transparent;
  border-color: transparent;
  color: blue;
  font-size: 30px;
}
.list-cont {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  background-color: white;
  width: 20%;
  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 8px;
  padding-right: 8px;

  margin: 3px;
  border-radius: 10px;
}
.list {
  color: black;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.li {
  list-style-type: none;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.button-remove {
  padding-left: 7px;
  padding-right: 7px;
  padding-top: 5px;
  padding-bottom: 5px;
  border-radius: 10px;
  background-color: transparent;
  border-color: transparent;

  padding-right: 5px;
  text-align: start;
}
